@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
  font-family: "PT Sans", "Arial", sans-serif;
}

.page-body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  margin: 0 auto;
  min-height: 100%;
  min-width: 320px;
}

@media (min-width: 1200px) {
  .page-body {
    max-width: 1200px;
  }
}
.header {
  display: flex;
  min-height: 56px;
  width: 100%;
  background-color: #ffffff;
}
.header--no-js {
  padding-bottom: 56px;
}

@media (min-width: 768px) {
  .header--no-js {
    padding-bottom: 0;
  }
}
.navigation {
  position: relative;
  width: 100%;
}

.navigation__site-logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.navigation__site-logo:hover {
  opacity: 0.6;
}
.navigation__site-logo:active {
  opacity: 0.3;
}

.navigation__logo-image {
  display: block;
}

.navigation__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto auto;
  background-color: #81b3d2;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.navigation__list--hidden {
  display: none;
}
.navigation__list--open {
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1;
}
.navigation__list--no-js {
  display: grid;
  position: static;
}

.navigation__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 55px;
  box-shadow: 0 -1px 0 0 #678fa8, 0 1px 0 0 #9ac2db;
}
.navigation__item:last-child::after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 7px;
  background: linear-gradient(transparent 0%, rgba(0, 0, 0, 0.2) 100%);
}

.navigation__list--no-js .navigation__item:last-child::after {
  background: none;
}

.navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
}
.navigation__link:hover {
  background-color: #669ec0;
}
.navigation__link:active {
  background-color: #5496bd;
  opacity: 0.2;
}

.navigation__menu-button {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.navigation__menu-button--no-js {
  display: none;
}
.navigation__menu-button:hover {
  opacity: 0.6;
}
.navigation__menu-button:active {
  opacity: 0.3;
}
.navigation__menu-button--open {
  z-index: 1;
}
.navigation__menu-button--close::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  color: #81b3d2;
  content: "";
  box-shadow: 0 9px 0 0, 0 18px 0 0;
}
.navigation__menu-button--open::before {
  position: absolute;
  top: -3px;
  left: 9px;
  content: "";
  width: 4px;
  height: 28px;
  transform: rotate(45deg);
  background-color: #ffffff;
}
.navigation__menu-button--open::after {
  position: absolute;
  top: -3px;
  left: 9px;
  content: "";
  width: 4px;
  height: 28px;
  transform: rotate(-45deg);
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .navigation__site-logo {
    top: 0;
  }
  .navigation__list {
    grid-template-columns: 1fr 1fr 156px 1fr 1fr;
    grid-template-rows: auto;
    background-color: #ffffff;
  }
  .navigation__list--hidden {
    display: grid;
  }
  .navigation__list--open {
    z-index: 0;
  }
  .navigation__item {
    box-shadow: none;
  }
  .navigation__item:last-child::after {
    background: none;
  }
  .navigation__item--left-edge {
    grid-column: 1/2;
  }
  .navigation__item--left-inner {
    grid-column: 2/3;
  }
  .navigation__item--right-inner {
    grid-column: 4/5;
  }
  .navigation__item--right-edge {
    grid-column: 5/6;
  }
  .navigation__link {
    font-size: 14px;
    line-height: 26px;
    color: #000000;
  }
  .navigation__link:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
  .navigation__link:active {
    background-color: #ffffff;
    color: #000000;
    opacity: 0.3;
  }
  .navigation__link--current {
    color: #766357;
  }
  .navigation__link--current:hover {
    color: #604e43;
  }
  .navigation__link--current:active {
    color: #766357;
    opacity: 0.3;
  }
  .navigation__menu-button {
    display: none;
  }
}
@media (min-width: 1200px) {
  .navigation__list {
    grid-template-columns: 1fr 1fr 244px 1fr 1fr;
  }
}
.footer {
  grid-row: 3/-1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto;
  justify-items: center;
  align-items: center;
  margin: 0;
  padding: 28px 20px 26px 20px;
  background-color: #ffffff;
}

.footer__link {
  padding: 0 15px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
}
.footer__link:hover {
  opacity: 0.6;
}
.footer__link:active {
  opacity: 0.3;
}

.footer__social-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 6px;
  margin: 0;
  padding: 33px 0 45px 0;
  list-style-type: none;
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 46px;
  height: 48px;
  background-color: #81b3d2;
}
.footer__social-link:hover {
  background-color: #669ec0;
}
.footer__social-link:active {
  background-color: #5496bd;
}
.footer__social-link:active svg {
  opacity: 0.3;
}

.footer__developer-link {
  position: relative;
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
  padding: 0 16px;
  text-decoration: none;
}
.footer__developer-link:hover .footer__developer-logo {
  fill: #81b3d2;
}
.footer__developer-link:active .footer__developer-logo {
  fill: #000000;
  opacity: 0.3;
}

.footer__developer-work {
  margin-right: 12px;
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
}

.footer__developer-logo {
  display: block;
}

@media (min-width: 768px) {
  .footer {
    grid-template-columns: 1fr 150px 1fr;
    grid-template-rows: auto;
    padding: 36px 45px;
  }
  .footer__link {
    justify-self: start;
  }
  .footer__social-list {
    padding: 0;
  }
  .footer__developer-link {
    justify-self: end;
  }
}
@media (min-width: 1200px) {
  .footer {
    padding: 36px 104px;
  }
}
.map-wrapper {
  position: relative;
  height: 350px;
}
.map-wrapper::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: url("../img/map-marker.svg");
}

.map__google {
  width: 100%;
  height: 350px;
  border: none;
}

.hotel-search {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 287px;
  padding: 33px 20px 49px 20px;
  box-sizing: border-box;
  background-color: #ffffff;
}

.hotel-search__title {
  margin: 0 0 20px 0;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
}

.hotel-search__text {
  display: inline-block;
  min-height: 72px;
  margin: 0 10px 27px 10px;
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
}

.hotel-search__text-braker {
  display: none;
}

.hotel-search__button {
  display: flex;
  justify-content: center;
  min-height: 58px;
  width: 100%;
  padding: 14px 32px;
  border: none;
  box-sizing: border-box;
  background-color: #81b3d2;
}
.hotel-search__button:hover {
  background-color: #669ec0;
}
.hotel-search__button:active {
  background-color: #5496bd;
}
.hotel-search__button:active .hotel-search__button-text {
  opacity: 0.3;
}
.hotel-search__button:disabled {
  background-color: #e5e5e5;
}

.hotel-search__button-text {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
}

@media (min-width: 768px) {
  .hotel-search {
    min-height: 318px;
    padding: 57px 155px 58px 155px;
  }
  .hotel-search__title {
    margin: 0 0 26px 0;
    font-size: 30px;
  }
  .hotel-search__text {
    margin: 0 0 37px 0;
    min-height: 48px;
  }
  .hotel-search__text-braker {
    display: inline;
  }
  .hotel-search__button {
    margin: 0 auto;
    width: 458px;
  }
}
@media (min-width: 1200px) {
  .hotel-search {
    min-height: 331px;
    padding: 57px 371px 66px 371px;
  }
  .hotel-search__title {
    margin: 0 0 25px 0;
  }
  .hotel-search__text {
    margin: 0 0 42px 0;
  }
}
.recommendations__list {
  display: grid;
  gap: 26px;
  padding: 35px 40px 35px 68px;
  margin: 0;
  list-style-type: none;
  background-color: #ffffff;
}

.recommendations__item {
  position: relative;
  min-height: 115px;
  background-color: #ffffff;
}

.recommendations__icon {
  position: absolute;
  top: -10px;
  left: -50px;
}

.reasons__text-wrapper {
  margin: 0 auto;
}

.recommendations__title {
  margin: 0 0 16px 0;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
}

.recommendations__text {
  min-height: 42px;
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
}

.recommendations__text-bracker {
  display: none;
}

@media (min-width: 768px) {
  .recommendations {
    grid-column-end: span 3;
  }
  .recommendations__list {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0;
    padding: 0;
  }
  .recommendations__item {
    min-height: 113px;
    padding: 158px 31px 59px 31px;
  }
  .recommendations__item--burger {
    grid-column-start: 2;
    grid-row-start: 1;
  }
  .recommendations__icon {
    position: absolute;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
  }
  .recommendations__title {
    margin: 0 0 22px 0;
    font-size: 21px;
    text-align: center;
  }
  .recommendations__text {
    min-height: 63px;
    text-align: center;
  }
  .recommendations__text-bracker {
    display: block;
  }
}
@media (min-width: 1200px) {
  .recommendations__item {
    min-height: 92px;
    padding: 158px 43px 80px 43px;
  }
  .recommendations__text {
    min-height: 42px;
  }
}
.reasons__list {
  display: grid;
  grid-template-columns: 1fr;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.reasons__title {
  margin: 0 0 11px 0;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
}

.reasons__title-bracker {
  display: none;
}

.reasons__number {
  margin: 0 0 17px 0;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  text-align: center;
}

.reasons__text {
  min-height: 42px;
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  text-align: center;
}

.reasons__item--blue {
  padding: 39px 20px 35px 20px;
  background-color: #81b3d2;
}
.reasons__item--blue .reasons__number, .reasons__item--blue .reasons__title, .reasons__item--blue .reasons__text {
  color: #ffffff;
}
.reasons__item--gray {
  padding: 25px 20px 24px 20px;
  background-color: #eeeeee;
}
.reasons__item--gray .reasons__text-bracker {
  display: none;
}
.reasons__item--graph {
  padding: 71px 12px 35px 13px;
  background-image: url("../img/graf.svg");
  background-repeat: no-repeat;
  background-position: top center;
}
.reasons__item--photo {
  width: 100vw;
  height: 260px;
}

.reasons__photo {
  display: block;
  width: 100%;
  object-fit: cover;
}

@media (min-width: 768px) {
  .reasons__list {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto auto auto;
  }
  .reasons__item--blue {
    padding: 43px 36px 41px 36px;
  }
  .reasons__item--photo {
    grid-column-end: span 2;
    width: auto;
    height: 256px;
  }
  .reasons__item--photo-bridge {
    grid-column-start: 1;
    grid-row-start: 3;
  }
  .reasons__item--blue-bridge {
    grid-column-start: 3;
  }
  .reasons__item--gray {
    min-height: 172px;
    padding: 42px 33px;
  }
  .reasons__item--gray .reasons__text {
    min-height: 63px;
  }
  .reasons__item--gray .reasons__text-bracker {
    display: block;
  }
  .reasons__item--graph {
    background-image: none;
    padding: 40px 32px 43px 32px;
  }
  .reasons__text-wrapper {
    width: 190px;
  }
  .reasons__number {
    min-height: none;
    margin-bottom: 22px;
  }
  .reasons__title {
    margin: 0 0 12px 0;
    font-size: 21px;
  }
  .reasons__title-bracker {
    display: block;
  }
}
@media (min-width: 1200px) {
  .reasons__item--blue {
    padding: 51px 78px 53px 79px;
  }
  .reasons__item--graph {
    padding: 47px 52px 56px 53px;
  }
  .reasons__item--gray .reasons__text-bracker {
    display: none;
  }
  .reasons__text-wrapper {
    width: 290px;
  }
}
.hero {
  padding: 0;
  background-color: #ffffff;
}

.hero__image-wrapper {
  display: flex;
  justify-content: center;
  min-height: 389px;
  padding: 90px 19px 85px 19px;
  box-sizing: border-box;
  background-color: #9ac2db;
  background-image: url("../../img/intro-triangle.svg"), url("../../img/back-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover, auto;
}

.hero__image {
  display: block;
}

.hero__text-wrapper {
  margin: 0 auto;
  width: 320px;
}

.hero__title {
  min-height: 84px;
  padding: 32px 25px 0 25px;
  margin: 0 0 26px 0;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}

.hero__text {
  min-height: 42px;
  padding: 0 30px 27px 30px;
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
  color: #333333;
}

@media (min-width: 768px) {
  .hero__image-wrapper {
    min-height: 518px;
    padding: 77px 154px 91px 154px;
    background-image: url("../../img/intro-triangle.svg"), url("../../img/back-desktop.jpg");
    background-size: auto, auto;
    background-position: bottom, top;
  }
  .hero__text-wrapper {
    width: 768px;
  }
  .hero__title {
    min-height: 52px;
    padding: 43px 160px 0 160px;
    font-size: 21px;
  }
  .hero__text {
    min-height: 26px;
    padding: 0 140px 52px 140px;
  }
}
@media (min-width: 1200px) {
  .hero__text-wrapper {
    width: 1200px;
  }
  .hero__title {
    padding: 43px 376px 0 376px;
  }
  .hero__text {
    padding: 0 356px 52px 356px;
  }
}